// main.js

// Query all sections and tabs
const sections = document.querySelectorAll('.section');
const navTabs = document.querySelectorAll('md-tab[data-target]');
const mdTabs = document.querySelector('md-tabs');

// Function to switch visible sections
const switchSection = (targetId, activeIndex) => {
  // Hide all sections
  sections.forEach((section) => {
    section.classList.remove('active');
  });

  // Show the target section
  const targetSection = document.getElementById(targetId);
  if (targetSection) {
    targetSection.classList.add('active');
    targetSection.scrollIntoView({ behavior: 'smooth' });
  }

  // Update active tab
  mdTabs.activeIndex = activeIndex;
};

// Add event listeners to all nav tabs
navTabs.forEach((tab, index) => {
  const targetId = tab.getAttribute('data-target');
  tab.addEventListener('click', (e) => {
    e.preventDefault();
    if (targetId === 'account') {
      // Redirect to dashboard.html for Account Portal
      window.location.href = './dashboard.html';
    } else {
      switchSection(targetId, index);
    }
  });
});

// Theme toggling
const toggleThemeButton = document.getElementById('toggle-theme');
toggleThemeButton.addEventListener('click', () => {
  document.documentElement.classList.toggle('light-theme');
  const isLightTheme = document.documentElement.classList.contains('light-theme');
  toggleThemeButton.querySelector('.material-symbols-outlined').textContent = isLightTheme ? 'light_mode' : 'dark_mode';
});

// Initialize the first section as active
switchSection('home', 0);
